
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'

import localStore from 'util/localStore'
export default {
data () {
return {
bg: require('../../assets/images/mine_bg.png'),
avatar: require('../../assets/images/avatar_img.png'),
qrcode: require('../../assets/images/qrcode.png'),
logo: '',
showQrcode: false,
userInfo: {},
userAccount: {},
ifLoginOut: false,
ifVisiable: localStore.get('TUP_accountVisible'),
pCitorganlevel: localStore.get('TUP_pCitorganlevel'),
storeCouponTotal: 0,
storeIntegralAmount: 0,
size: 128,
pCitoutletproperty2: localStore.get('TUP_pInfo').pCitoutletproperty2,
auditStatus: '' 
}
},
methods: {
next (param) {
this.$router.push({
path: '/' + param
})
},
toNative (int) {
this.$util.getMachine(int)
},
toRecharge () {
this.$router.push({
path: '/recharge'
})
},
changeVisible () {
this.ifVisiable = !this.ifVisiable
localStore.set('TUP_accountVisible', this.ifVisiable)
},
loginOut () {
this.ifLoginOut = true
},
toApply () {
this.$router.push({
path: '/openApply'
})
},
toApplyProgress () {
this.$router.push({
path: '/openApplyProgress'
})
},
toInfo () {
this.$router.push({
path: '/basicInfo'
})
},
onConfirm () {

localStore.remove('TUP_token')
this.$router.push({
path: '/login'
})
},
toOrder (i) {
this.$router.push({
path: '/order',
query: {
index: i
}
})
},
formatePrice (num) {
return num / 100
},
_getInfo () {
this.$tupHttp(this, 'front/user/info/v1').then(res => {
this.userInfo = res.data.model
if (this.userInfo.pOutletStatus === '已下首订未开业') {
this.$tupHttp(this, 'front/user/openning/info').then(rs => { // null:没有提交开业申请
this.auditStatus = rs.data.model === null ? null : rs.data.model.auditStatus
})
}
this.userAccount = this.userInfo.userAccount
}, () => {
this.$util.getMachine(8)
}).catch(() => {
this.$util.getMachine(8)
})
},
_getHsy () {
this.$tupHttp(this, 'front/integralAccount/findIntegralAndCouponByCode?storeCode=' + localStore.get('TUP_pUid')).then(res => {
this.storeIntegralAmount = res.data.model.storeIntegralAmount || 0
this.storeCouponTotal = res.data.model.storeCouponTotal || 0
}, () => {
this.$util.getMachine(8)
}).catch(() => {
this.$util.getMachine(8)
})
},
_getCode () {
this.$tupHttp(this, 'front/pos/order/order/logo/' + localStore.get('TUP_pUid')).then(res => {
this.logo = res.data.model.link
}, () => {
this.$util.getMachine(8)
}).catch(() => {
this.$util.getMachine(8)
})
}
},
created () {
this._getInfo()
this._getHsy()
this._getCode()
},
components: {
XDialog,
Qrcode
}
}



